import { Nav,Button } from "react-bootstrap"
import { Link } from "react-router-dom"
import App from "../App"
import CartWidget from "./CartWidget"

const NavBar = () =>{

    return(
        <>

        <Nav>
            <div className="container navBar">
                <div className='logo'>
                    <Link to='/' element={<App/>}>
                        <img src="data:image/png;base64," alt="imagen del logo de la empresa" />
                    </Link>
                </div>
                <div className="container">
                    <Link to='/Adder'>
                        <Button variant='primary'> Agregar productos</Button>
                    </Link>
                    <Link to='/Contacto'>
                        <Button variant='primary'>Contacto</Button>
                    </Link>
                    <Link to='/Galeria'>
                        <Button variant='primary'>Galeria</Button>
                    </Link>
                    <Link to='/Nosotros'>
                        <Button variant='primary'>Nosotros</Button>
                    </Link>
                </div>
            <div className='container contenedorCarrito'>
                <Link to='/Cart'>
                    <CartWidget />
                </Link>
            </div>
            </div>

        </Nav>
        </>
    )
}
export default NavBar 